@import './years_mileage'; // wrapper

.choice-car {
	display: flex;
	flex-direction: column;

	&__blocks {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		gap: 20px;
		@include adaptiv-value-in-min(margin-left, -99, 0, 940, 375);
		@include adaptiv-value-in-min(margin-right, -99, 0, 940, 375);
		@media screen and (max-width: 800px) {
			justify-items: center;
			grid-template-columns: repeat(4, 1fr);
		}

		// @include adaptiv-value-in-min(padding-left, 99, 15, 940, 375);
		// @include adaptiv-value-in-min(padding-right, 99, 15, 940, 375);
		// @include adaptiv-value(padding-top, 10, 15, 1);
		// @include adaptiv-value(padding-bottom, 10, 15, 1);
	}

	&__block {
		display: flex;
		flex-direction: column;
		grid-column: 3 span;
		@media screen and (max-width: 800px) {
			grid-column: 4 span;
			width: 100%;
		}
		
		border-radius: 10px;
		background-color: rgba(0, 0, 0, 0.03);
		transition: all 0.3s ease;
		cursor: pointer;

		@media screen and (max-width: 500px) {
			background-color: transparent;
		}
	}

	&__img {
		position: relative;
	}

	&__img-watermark{
		position: absolute;
		right: 31px;
		top: 25px;
		z-index: 25;
		display: flex;
		align-items: center;
		gap: 11px;
		@media screen and (max-width: 800px) {
			right: 20px;
			top: 18px;
		}

		.watermark-text{
			font-weight: 700;
			font-size: 16px;
			line-height: 135%;
			color: var(--text-black);
		}

		.watermark-icon{
			@media screen and (max-width: 800px) {
				width: 18.521px;
				height: 18.111px;
			}
		}
	}

	&__swiper {
		position: relative;
		display: block;
		overflow: hidden;

		// width: 100%;
		// height: 100%;
		width: 100%;
		height: 326px;
	}

	&__wrapper {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 20;
		display: flex;
		width: 100%;
		height: 100%;
	}

	&__slide {
		flex-grow: 1;
		cursor: pointer;
	}

	&__slide-img {
		position: absolute;
		left: 50%;
		top: 0;
		z-index: 2;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;

		// background-color: var(--red-light);
		opacity: 0;
		transform: translateX(-50%);
		pointer-events: none;
	}

	&__slide-img img {
		border-radius: 10px;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__slide:first-child .choice-car__slide-img {
		z-index: -1;
		opacity: 1;
	}

	&__slide:hover .choice-car__slide-img {
		z-index: -1;
		opacity: 1;
	}

	// инфо
	&__info {
		display: flex;
		flex-direction: column;
		padding: 20px;
		@media screen and (max-width: 500px) {
			padding-right: 0;
			padding-left: 0;
		}
	}

	&__title {
		font-weight: 700;
		font-size: 24px;
		line-height: 130%;
		color: var(--text-black);
	}

	&__price {
		display: flex;
		align-items: center;
		margin-top: 10px;
		margin-bottom: 17px;
		@media screen and (max-width: 480px) {
			margin-top: 5px;
		}
	}

	&__price-total {
		font-weight: 700;
		font-size: 24px;
		line-height: 130%;
		color: #E63535; 
	}

	// &__discount {
	// 	margin-right: 15px;
	// }

	&__price-old {
		margin-left: 15px;
		font-weight: 400;
		font-size: 16px;
		line-height: 135%;
		text-decoration-line: line-through;
		color: var(--text-grey);
	}

	&__conditions {
		display: flex;
		font-weight: 700;
		font-size: 16px;
		line-height: 135%;
		color: #727171;
		gap: 12px;

		.condition{
			border-radius: 25px;
			padding: 3px 7px;
			background: var(--white);
			@media screen and (max-width: 490px) {
				background-color: #F7F7F7;
			}
		}
	}

	&__mileage-owner {
		display: flex;
		margin-top: 15px;
		margin-bottom: 7px;
		font-weight: 400;
		font-size: 18px;
		line-height: 135%;
		color: var(--text-grey);
		@media screen and (max-width: 800px) {
			margin-top: 11px;
			margin-bottom: 5px;
		}
	}

	&__mileage {
		margin-right: 25px;
	}

	&__charact {
		font-weight: 400;
		font-size: 18px;
		line-height: 135%;
		color: var(--text-grey);
	}

	&__btn-buy {
		margin-top: 25px;
		margin-bottom: 15px;
		border-radius: 51px;
		padding: 20px 115px;
		font-weight: 500;
		font-size: 19px;
		line-height: 135%;
		text-align: center;
		color: var(--white);
		background-color: var(--primary-color);
		cursor: pointer;
		@media screen and (max-width: 800px) {
			margin-top: 19px;
			padding-right: 20px;
			padding-left: 20px;
			width: 100%;
			font-size: 18px;
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__tel-text {
		display: flex;
		flex-direction: column;
		margin-left: 16px;
		@media screen and (max-width: 930px) {
			margin-left: 11px;
		}
	}

	&__tel {
		position: relative;
		font-weight: 700;
		font-size: 19px;
		line-height: 130%;
		color: var(--text-black);
		cursor: pointer;
		@media screen and (max-width: 930px) {
			font-size: 18px;
		}

		&.dot::before{
			left: -13px;
			top: 10px;
			width: 6px;
			height: 6px;
			@media screen and (max-width: 930px) {
				left: -11px;
				top: 11px;
			}
		}
	}

	&__tel-under {
		font-weight: 400;
		font-size: 16px;
		line-height: 135%;
		color: var(--text-grey);
		@media screen and (max-width: 930px) {
			font-size: 14px;
		}
	}

	&__btn-order {
		border-radius: 62px;
		padding: 18px 30px;
		font-weight: 500;
		font-size: 18px;
		line-height: 135%;
		text-align: center;
		text-decoration-line: underline;
		color: rgba(40, 38, 38, 0.70);
		background-color: var(--white);
		cursor: pointer;
		@media screen and (max-width: 930px) {
			padding-right: 21px;
			padding-left: 21px;
			font-size: 16px;
		}

		@media screen and (max-width: 500px) {
			background-color: #F7F7F7;
		}
	}
	

	&__block:hover,
	&__block.active {
		background-color: var(--red-light);

		.choice-car__title {
			color: var(--primary-color);
		}

		.choice-car__tel {
			color: var(--primary-color);
			background-color: transparent;
		}

		.choice-car__mileage-owner {
			color: var(--text-black);
		}

		.choice-car__charact {
			color: var(--text-black);
		}
	}

	&__btn {
		width: 100%;
		max-width: 700px;
		@include adaptiv-value(margin-top, 20, 15, 1);
	}
}

